<i18n>
{
  "en": {
    "BillingRules": "Billing rules",
    "TimeSharing": "Time sharing billing",
    "TimeSharingText": "Time started after lending, free for the first 30 minutes; The excess part shall be charged at one yuan per hour, and if it is less than one hour, it shall be charged at one hour. ￥5 per 24 hours capped.",
    "DepositRules": "Deposit rules",
    "DepositRulesText": "The account balance must be at least 99 yuan before renting the battery. (of which, 99 yuan will be the default deposit. If the account balance is less than 99, the order amount will be deducted from 99. If the account balance is greater than 99, the order amount will be deducted from the amount of 99.)",
    "DepositRulesLastText": "After the battery is returned, the balance can be withdrawn at any time in my wallet. When the lease is too long and the rent reaches 99 yuan, it will be deducted automatically from the account balance.",
    "BorrowBattery": "Borrow a battery"
  },
  "zh": {
    "BillingRules": "计费原则",
    "TimeSharing": "分时计费",
    "TimeSharingText": "自借出后开始计时,前30分钟内免费;超出部分按每小时1元计费,不足1小时按1小时计算;每24小时5元封顶",
    "DepositRules": "押金规则",
    "DepositRulesText": "租借充电宝前需保证账户余额至少有99元。（其中99元将默认作为押金，若账户余额小于99，则订单金额从99中扣除；若账户余额大于99，则订单金额从99外的金额扣除）",
    "DepositRulesLastText": "充电宝归还后,余额可随时在“我的钱包”提现。当租借时间过长,租金达到99元时,将自动从账户余额中扣除",
    "BorrowBattery": "知道了，去借充电宝"
  }
}
</i18n>

<template>
  <div class="charging">
    <head-top :title='$t("BillingRules")' :go-back="true"></head-top>
    <div class="container">
      <div class="describe">
        <strong>{{ $t('TimeSharing') }}</strong>
        <span>{{ $t('TimeSharingText') }}</span>
      </div>
      <div class="describe">
        <strong>{{ $t('DepositRules') }}</strong>
        <span>{{ $t('DepositRulesText') }}</span>
        <span>{{ $t('DepositRulesLastText') }}</span>
      </div>
      <button @click="goHandle" :class="{cwhite: Version === 2}">{{ $t('BorrowBattery') }}</button>
    </div>
  </div>
</template>

<script>
import headTop from '../../../components/Header/index'
export default {
  name: 'charging',
  data () {
    return {}
  },

  components: {
    headTop
  },

  methods: {
    goHandle () {
      this.$router.push({path: '/takingCharger'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.charging{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .describe{
      .wh(100%, 220px);
      text-align: left;
      padding: 30px 5%;
      strong{
        display: block;
        font-weight: bold;
        font-size: 30px;
        color: #585858;
      }
      span{
        display: block;
        font-size: 26px;
        color: #989898;
        line-height: 35px;
        margin-top: 30px;
      }
    }
    button{
      .wh(690px, 100px);
      background: @themeColor;
      border-radius: 10px;
      font-size: 30px;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -345px;
      // box-shadow: 0px 5px 10px rgba(244, 218, 33, 0.6)
      box-shadow: 0px 4px 25px @themeColor
    }
    .cwhite{
      color: #FFF;
    }
  }
}
</style>



